	module.exports = {"data":{"post":{"timeToRead":1,"canonicalURL":"https://blog.iansinnott.com/custom-post-types-a-great/","html":"<h4 id=\"justin-tadlock-is-a-boss\"><a href=\"#justin-tadlock-is-a-boss\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Justin Tadlock is a boss</h4>\n<p>I just read an article (link below) of his on custom post types in WordPress that was illuminating. Also, it turns out he is another developer that jumped ship for Asia (he's in Seoul).</p>\n<p>So where was I. Ah, custom posts. Custom posts provide great flexibility for WP developers and a simple user experience for the client. This article is the primary reason I decided to use this feature for my latest client project. I'll be using custom posts to handle addresses for company that has about 20 branches spread over three cities. Nothing too exciting but this will allow simple edits and updates as the company expands. That's all for now, must sleep...</p>\n<p>Oh and here's <a href=\"http://justintadlock.com/archives/2010/04/29/custom-post-types-in-wordpress\">the link</a> for anyone interested. Enjoy.</p>","frontmatter":{"created":"2012-12-18T08:00:00.000Z","title":"Custom Post Types: A Great Article","disqusId":[979369744,979369744]}}},"pathContext":{"id":"/Users/jandrix/dev/sites/blog.iansinnott.com/content/_posts/2012-12-18-custom-post-types-a-great.md absPath of file >>> MarkdownRemark","next":{"frontmatter":{"title":"WordPress Theming From Scratch","created":"2013-01-11T08:00:00.000Z"},"slug":"wordpress-theming-from-scratch"},"prev":null}}

